/**
 * @des 领取中心
 *
 * @author stav stavyan@qq.com
 * @date 2020-01-13 11:28
 * @copyright 2019
 */
import {couponList, couponReceive} from "@/api/userInfo";
import rfLoadMore from '@/components/rf-load-more/rf-load-more';

import moment from '@/utils/moment';

export default {
    components: {
        rfLoadMore
    },
    data() {
        return {
            couponList: [],
            type: null,
            loadingType: 'more',
            token: null,
            page: 1,
            loading: true,
            errorInfo: '',
        }
    },
    filters: {
        time(val) {
            return moment(val * 1000).format('YYYY-MM-DD HH:mm')
        }
    },
    onLoad(options) {
        this.type = options.type;
        this.initData();
    },
    //下拉刷新
    onPullDownRefresh() {
        this.page = 1;
        this.couponList.length = 0;
        this.getCouponList('refresh');
    },
    //加载更多
    onReachBottom() {
        this.page++;
        this.getCouponList();
    },
    methods: {
        // 数据初始化
        initData() {
            this.getCouponList();
        },
        // 统一跳转接口
        navTo(url, type) {
            if (!this.token) {
                url = '/pages/public/logintype';
            }
            if (type) {
                uni.switchTab({url});
            }
            if (url === 'login') {
                return
            } else {
                uni.navigateTo({
                    url
                })
            }
        },
        /**
         *@des 获取收货地址列表
         *@author stav stavyan@qq.com
         *@blog https://stavtop.club
         *@date 2019/11/18 09:58:15
         */
        async getCouponList(type) {
            await this.$get(`${couponList}`, {
                page: this.page
            }).then(r => {
                this.loading = false;
                if (type === 'refresh') {
                    uni.stopPullDownRefresh();
                }
                this.loadingType = r.data.length === 10 ? 'more' : 'nomore';
                this.couponList = [...this.couponList, ...r.data];
            }).catch(err => {
                console.log(11)
                this.couponList.length = 0;
                this.errorInfo = err;
                this.loading = false;
                if (type === 'refresh') {
                    uni.stopPullDownRefresh();
                }
            })
        },
        /**
         *@des 获取优惠券
         *@author stav stavyan@qq.com
         *@blog https://stavtop.club
         *@date 2019/11/25 13:41:19
         */
        async getCoupon(item) {
            if (this.type) return;
            if (parseInt(item.is_get, 10) === 0) {
                this.$api.msg('该优惠券不可领取');
                return;
            }
            await this.$post(`${couponReceive}`, {
                id: item.id
            }).then(r => {
                this.page = 1;
                this.couponList = [];
                this.getCouponList();
                this.$api.msg('领取成功');
            }).catch(err => {
                console.log(err)
            })
        }
    }
}
